import React, { useContext, useEffect } from "react";
import { toast } from "react-toastify";
import { providerName } from "../../../consts";
import { WalletContext } from "../../contexts/wallet-context";
import { CloseButton } from "../close-button";
import { Popup } from "../popup";
import "./style.css";

interface Props {
  open: boolean;
  onClose: () => void;
}

export const SelectWallet = ({ onClose, open }: Props) => {
  const { active, error, activate, connectorOptions, connectors } =
    useContext(WalletContext);

  const filterConnections = () => {
    if (!window.ethereum) {
      return connectorOptions.filter((c) => c.type !== connectors.injected);
    }
    return connectorOptions;
  };

  useEffect(() => {
    if (!error) {
      return;
    }
    if (error.message === "The user rejected the request.") {
      toast.error(error.message);
    }
  }, [error]);

  const connect = (connector: any, type: string) => {
    activate(connector);
    window.localStorage.setItem(providerName, type);
  };

  const select = (connector: any, type: string) => {
    try {
      connect(connector, type);
    } catch (error) {
      console.log(error);
    }
    onClose();
  };

  useEffect(() => {
    if (active) {
      onClose();
    }
  }, [active, onClose]);

  return (
    <Popup
      open={open}
      onClose={onClose}
      className="wallet-select font-[Montserrat]"
    >
      <>
        <div className="flex items-center p-4">
          <h3 className="grow">Select Wallet</h3>
          <CloseButton onClick={onClose} />
        </div>
        <hr className="border-top w-full" />
        <ul className="wallet-select-list">
          {filterConnections().map((connector: any) => (
            <li
              key={connector.text}
              onClick={() => {
                select(connector.type, connector.name);
              }}
            >
              <img alt="" src={connector.image} />
              <p>{connector.text}</p>
            </li>
          ))}
        </ul>
      </>
    </Popup>
  );
};
