import React, {
  createContext,
  ReactNode,
  useEffect,
  useMemo,
  useState,
} from "react";
import { useWeb3React } from "@web3-react/core";
import { InjectedConnector } from "@web3-react/injected-connector";
import { AbstractConnector } from "@web3-react/abstract-connector";
import { isNil, noop } from "lodash";
import { WalletConnectConnector } from "@web3-react/walletconnect-connector";
import { useLocalStorage } from "../hooks/use-local-storage.hook";
import metamaskLogo from "../../assets/images/metamask.png";
import walletConnectLogo from "../../assets/images/walletconnect.png";
import coinbaseLogo from "../../assets/images/coinbase.svg";

type ConnectorOption = {
  type: AbstractConnector;
  name: string;
  text: string;
  image: string;
};

type Connectors = {
  injected: InjectedConnector;
  walletConnect: WalletConnectConnector;
};

export namespace WalletContext {
  export type Value = {
    active: boolean;
    error: Error;
    activate: (
      connector: AbstractConnector,
      onError?: (error: Error) => void,
      throwErrors?: boolean
    ) => void;
    deactivate: () => void;
    account: string;
    showWalletSelector: boolean;
    setShowWalletSelector: (show: boolean) => void;
    connectorOptions: ConnectorOption[];
    connectors: Connectors;
    library: any;
  };
}

export const WalletContext = createContext<WalletContext.Value>({
  account: null,
  active: false,
  error: null,
  activate: noop,
  deactivate: noop,
  showWalletSelector: false,
  setShowWalletSelector: noop,
  connectorOptions: [],
  connectors: null,
  library: null,
});
WalletContext.displayName = "WalletContext";

export const WalletProvider = ({ children }: { children: ReactNode }) => {
  const { account, active, error, activate, deactivate, library } =
    useWeb3React();
  const [walletConnected, setWalletConnected] = useLocalStorage<boolean>(
    "walletConnected",
    false
  );
  const [loaded, setLoaded] = useState(false);
  const [showWalletSelector, setShowWalletSelector] = useState(false);

  const wrappedActivate = useMemo(
    () =>
      (
        connector: AbstractConnector,
        onError?: (error: Error) => void,
        throwErrors?: boolean
      ) => {
        activate(connector, onError, throwErrors);
        setWalletConnected(true);
      },
    [activate, setWalletConnected]
  );

  const wrappedDeactivate = useMemo(
    () => () => {
      deactivate();
      setWalletConnected(false);
    },
    [deactivate, setWalletConnected]
  );

  const connectors = useMemo(
    () => ({
      injected: new InjectedConnector({
        supportedChainIds: [
          1, // Mainet
          5, // Goerli
          1337, // Hardhat
        ],
      }),
      walletConnect: new WalletConnectConnector({
        infuraId: "8d9b86071b8d4247a2a2ac11c75b2308",
        bridge: "https://bridge.walletconnect.org",
        qrcode: true,
      }),
    }),
    []
  );

  const connectorOptions: ConnectorOption[] = useMemo(
    () => [
      {
        type: connectors.injected,
        name: "injected",
        text: "Metamask",
        image: metamaskLogo,
      },
      {
        type: connectors.injected,
        name: "injected",
        text: "Coinbase",
        image: coinbaseLogo,
      },
      {
        type: connectors.walletConnect,
        name: "walletConnect",
        text: "Wallet Connect",
        image: walletConnectLogo,
      },
    ],
    [connectors]
  );

  useEffect(() => {
    connectors.injected
      .isAuthorized()
      .then((isAuthorized) => {
        setLoaded(true);
        if (isAuthorized && !active && isNil(error) && walletConnected) {
          activate(connectors.injected);
        }
      })
      .catch(() => {
        setLoaded(true);
      });
  }, [
    connectors,
    activate,
    active,
    error,
    walletConnected,
    setWalletConnected,
  ]);

  const value = useMemo(
    () => ({
      account: account as string,
      active: active,
      error: error,
      activate: wrappedActivate,
      deactivate: wrappedDeactivate,
      showWalletSelector: showWalletSelector,
      setShowWalletSelector: setShowWalletSelector,
      connectorOptions: connectorOptions,
      connectors: connectors,
      library: library,
    }),
    [
      account,
      active,
      error,
      wrappedActivate,
      wrappedDeactivate,
      showWalletSelector,
      setShowWalletSelector,
      connectorOptions,
      connectors,
      library,
    ]
  );

  return loaded ? (
    <WalletContext.Provider value={value}>{children}</WalletContext.Provider>
  ) : null;
};
